.container {
    margin: 0 auto;

    @media screen and (min-width: 1200px) {
        max-width: 1170px;
    }

    @include breakpoint(small) {
        max-width: 100%;
    }
}

.cursive {
    font-family: $font-tertiary;
    font-weight: normal;
}

.site {
    overflow: hidden;
}

.global-link-strip {
    padding: 20px 0 18px;
    background-color: $light_grey;

    @include breakpoint(medium) {
        padding: 20px 0 0;
    }

    @include breakpoint(small) {
        padding-bottom: 19px;
    }

    .usp-lists {
        margin: 0;
        padding: 0 0 0 24px;

        @media screen and (min-width: 768px) {
            display: flex;
            justify-content: space-between;
        }

        @media screen and (max-width: 1299px) {
            padding: 0 24px;
            // justify-content: unset;
            // align-items: center;
        }

        @include breakpoint(wide) {
            justify-content: unset;
            flex-wrap: wrap;
            padding: 0;
        }

        .usp-list {
            position: relative;
            // padding-left: 29px;
            font-size: 15px;
            font-family: $font-secondary;
            font-weight: 600;
            color: $color_text;
            list-style: none;

            @include breakpoint(wide) {
                flex: 25%;
                max-width: 25%;
                padding-right: 10px;
            }

            @include breakpoint(medium) {
                flex: 50%;
                max-width: 50%;
                margin: 0 0 20px;
            }

            @include breakpoint(small) {
                flex: unset;
                max-width: unset;
                padding-right: 0;
                text-align: center;
                margin: 0;
            }

            &:not(:first-child) {
                @include breakpoint(small) {
                    display: none;
                }
            }

            img {
                margin-right: 5px;

                @include breakpoint(small) {
                    display: inline-block;
                }
            }
        }
    }
}


/* why-readymix */
.why-readymix {
    padding-bottom: 79px;
    clear: both;

    @include breakpoint(medium) {
        padding-bottom: 50px;
    }

    @include breakpoint(small) {
        padding-bottom: 36px;
    }

    .title-row {
        padding: 70px 0 50px;

        @include breakpoint(medium) {
            padding: 35px 0 45px;
        }

        @include breakpoint(small) {
            padding: 31px 0 43px;
        }

        h2 {
            margin: 0;
            max-width: 331px;

            @include breakpoint(medium) {
                max-width: 100%;
            }

            br {
                @media screen and (min-width: 568px) {
                    display: none;
                }

                @media screen and (min-width: 992px) and (max-width: 11199px) {
                    display: block;
                }
            }

            span {
                columns: $dark_pink;
            }
        }

        p {
            margin: 0;
            color: $color_text;
            font-family: $font-primary;
            font-weight: 400;
            padding-top: 5px;
            max-width: 550px;

            @include breakpoint(medium) {
                max-width: 100%;
                padding-top: 14px;
            }
        }
    }
}

.grid-tiles {
    .container {
        .grid-wrap {
            margin: 0 -6px;

            @include breakpoint(small) {
                display: flex;
                flex-wrap: wrap;
                justify-content: space-between;
                margin: 0;
            }

            &:after {
                content: '';
                display: block;
                clear: both;
            }
        }
    }

    .tile {
        height: 328px;
        margin: 6px;
        text-align: center;
        width: calc(50% - 12px);
        float: left;
        position: relative;
        border-radius: $border-radius;
        overflow: hidden;
        -webkit-mask-image: -webkit-radial-gradient(white, black);
        display: flex;
        // align-items: center;
        align-items: flex-end;

        @include breakpoint(medium) {
            height: 220px;
        }

        @include breakpoint(small) {
            height: 160px;
            max-width: 49%;
            width: 49%;
            flex: 0 0 49%;
            margin: 0;
            margin-bottom: 6px;
            border-radius: $border-radius;
        }

        &:nth-child(1),
        &:nth-child(3) {
            width: calc(25% - 12px);

            @include breakpoint(small) {
                max-width: 49%;
                width: 49%;
                flex: 0 0 49%;
            }
        }

        &:nth-child(1) {
            @include breakpoint(small) {
                order: 2;
            }
        }

        &:nth-child(2) {
            @include breakpoint(small) {
                order: 1;
                max-width: 100%;
                width: 100%;
                flex: 0 0 100%;
            }
        }

        &:nth-child(3) {
            @include breakpoint(small) {
                order: 3;
            }
        }

        &:nth-child(4) {
            @include breakpoint(small) {
                order: 4;
            }
        }

        &:nth-child(5) {
            @include breakpoint(small) {
                order: 5;
            }
        }
        .bg-image {
            position: absolute;
            height: 100%;
            width: 100%;
            left: 0;
            top: 0;
            @include transition(0.8s);
            transform-origin: center;
            object-fit: cover;
            transform: scale(1.05);
        }

        .tile-content {
            position: relative;
            padding: 20px;

            @include breakpoint(medium) {
                padding: 15px;
            }

            @include breakpoint(extrasmall) {
                padding: 10px;
            }

            h3 {
                color: $white;
                text-shadow: 0px 1px 18px rgba($black, 0.5);
                line-height: 1.15;
                font-weight: 600;

                @include breakpoint(medium) {
                    font-size: 22px;
                }

                @include breakpoint(small) {
                    font-size: 20px;
                    margin: 0;
                }

                @media screen and (max-width: 358px) {
                    font-size: 16px;
                }
            }

            p {
                color: $white;
                // font-family: $font-secondary;
                margin: 0;

                @include breakpoint(medium) {
                    display: none;
                }
            }
        }

        .overlay {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background: transparent;
            @include transition(0.4s);
        }

        &:hover {
            .overlay {
                background: rgba(0, 0, 0, 0.42);
            }

            .bg-image {
                transform: scale(1.1);
            }
        }

        .abs-link {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background: transparent;
            z-index: 1;
        }
    }
}

/* end why-readymix */

/* parallax-cta */
.parallax-cta {
    position: relative;
    padding: 53px 0 49px;
    min-height: 500px;
    height: auto;
    display: flex;
    align-items: center;
    overflow: hidden;

    @include breakpoint(small) {
        min-height: unset;
        height: auto;
        padding: 41px 0 40px;
    }

    &:before {
        content: '';
        position: absolute;
        height: 100%;
        width: 100%;
        top: 0;
        left: 0;
        background: rgba(0, 0, 0, 0.1);
        z-index: 1;
    }

    .container {
        position: relative;
        z-index: 2;

        @include breakpoint(small) {
            padding: 0 30px;
        }
    }

    .cta-box {
        max-width: 448px;
        padding: 40px 41px 33px;
        border-radius: $border-radius;
        background-color: $dark_pink;
        text-align: center;

        @include breakpoint(wide) {
            margin: 0 auto;
        }

        @include breakpoint(small) {
            padding: 24px 24px 20px;
            margin: 0 15px;
            border-radius: $border-radius;
        }

        h3 {
            color: $white;
            margin-bottom: 8px;

            @include breakpoint(small) {
                font-size: 20px;
                line-height: 1.2;
                margin-bottom: 10px;
            }
        }

        .btn {
            min-width: 200px;

            &:hover {
                background: $dark_pink;
                color: $white;
            }
        }

        p {
            margin-bottom: 20px;
            color: $white;

            @include breakpoint(small) {
                margin: 0;
            }
        }
    }

    .bcg {
        @include center();
        height: 100%;
        width: 100%;
        background-size: cover;
    }

    &.cta-right {
        .cta-box {
            float: right;

            @include breakpoint(wide) {
                float: none;
                margin: 0 auto;
            }
        }
    }

    .absolute-background-image {
        img {
            transform: scale(1.2);

            @include breakpoint(small) {
                display: none;
                transform: scale(1);
            }

            &.img-mobile {
                display: none;

                @include breakpoint(small) {
                    display: block;
                }
            }
        }
    }
}

/* showcase-layout */
.showcase-layout {
    padding-top: 76px;

    @media screen and (min-width: 768px) {
        margin: 0 -3px;
    }

    @include breakpoint(small) {
        background-color: $white;
        padding-top: 26px;
    }

    .container {
        width: 1446px;
        max-width: 100%;
        padding: 0;

        &:after {
            content: '';
            clear: both;
            display: table;
        }
    }

    .title-row {
        .container {
            display: block !important;
            height: auto !important;
            width: 780px;
            max-width: 100%;
            padding: 0 15px;
        }

        .title-content {
            padding-bottom: 47px;

            @include breakpoint(small) {
                padding-bottom: 19px;
            }

            h2 {
                br {
                    @media screen and (min-width: 568px) {
                        display: none;
                    }
                }
            }
        }
    }

    .box {
        height: 452px;
        margin: 3px;
        border-radius: $border-radius;
        float: left;
        position: relative;
        overflow: hidden;
        -webkit-mask-image: -webkit-radial-gradient(white, black);

        @include breakpoint(wide) {
            height: 350px;
        }

        @include breakpoint(medium) {
            height: 240px;
        }

        @include breakpoint(small) {
            height: 146px;
            margin: 0 0 10px 0;
        }

        .bg-image {
            display: block;
            position: absolute;
            height: 100%;
            width: 100%;
            left: 0;
            top: 0;
            @include transition(0.8s);
            filter: saturate(0.7);
            object-fit: cover;
        }

        &:before {
            content: '';
            position: absolute;
            width: 100%;
            height: 40%;
            bottom: 0;
            left: 0;
            z-index: 1;
            opacity: 0.8;
            @include transition(0.8s);
            /* Permalink - use to edit and share this gradient: https://colorzilla.com/gradient-editor/#2d3648+0,2d3648+100&0+0,0.6+100 */
            background: -moz-linear-gradient(top, rgba(45, 54, 72, 0) 0%, rgba(45, 54, 72, 0.6) 100%);
            /* FF3.6-15 */
            background: -webkit-linear-gradient(top, rgba(45, 54, 72, 0) 0%, rgba(45, 54, 72, 0.6) 100%);
            /* Chrome10-25,Safari5.1-6 */
            background: linear-gradient(to bottom, rgba(45, 54, 72, 0) 0%, rgba(45, 54, 72, 0.6) 100%);
            /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
            filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#002d3648', endColorstr='#992d3648', GradientType=0);
            /* IE6-9 */
        }

        &:hover {
            .bg-image {
                transform: scale(1.05);
                transform-origin: center;
            }

            &:before {
                opacity: 1;
            }

            .link-text {

                img,
                .svg,
                svg {
                    -webkit-transform: translate(5px, 0);
                    transform: translate(5px, 0);
                }
            }
        }

        .link-text {
            font-size: 22px;
            line-height: 1.23;
            text-align: right;
            color: $white;
            position: absolute;
            bottom: 25px;
            right: 20px;
            z-index: 3;

            &:hover {
                color: $white;
            }

            @include breakpoint(medium) {
                font-size: 16px;
                bottom: 15px;
            }

            @include breakpoint(small) {
                font-size: 16px;
                bottom: 5px;
            }

            @include breakpoint(iPhone5) {
                font-size: 14px;
            }

            img,
            .svg,
            svg {
                @include transition(0.4s);
            }

            &:hover {

                img,
                .svg,
                svg {
                    transform: translate(5px, 0);
                }
            }
        }

        .abs-content {
            position: absolute;
            left: 15px;
            bottom: 15px;
            z-index: 1;

            h6 {
                margin: 0;
                font-size: 14.3px;
                font-weight: 500;
                line-height: 1.28;
                color: $white;

                @include breakpoint(small) {
                    font-size: 12.7px;
                }
            }
        }

        .abs-pop-icon {
            position: absolute;
            right: 12px;
            top: 5px;

            @include breakpoint(small) {
                right: 7px;
                top: 0;
                width: 15px;
            }

            .svg {
                width: 15px;
                height: 15px;
            }
        }

        &.video-link12 {
            &:after {
                content: '';
                position: absolute;
                top: 50%;
                left: 50%;
                -webkit-transform: translate(-50%, -50%);
                transform: translate(-50%, -50%);
                width: 63px;
                height: 63px;
                background: url(../../images/icons-play-2.svg) no-repeat center;
                background-size: cover;
                z-index: 1;
            }
        }
    }

    &.option-3 {
        .box-1 {
            width: calc(60% - 3px);
            margin-left: 0;

            @include breakpoint(small) {
                width: 100%
            }
        }

        .box-2 {
            width: calc(40% - 3px);
            margin-right: 0;

            @include breakpoint(small) {
                width: 100%
            }
        }

        .box-3 {
            // width: calc(100% - 6px);
            width: 100%;
            margin-left: 0;
            margin-right: 0;

            @include breakpoint(small) {
                width: 100%
            }
        }

        .abs-content {
            h6 {
                @media screen and (min-width: 1000px) {
                    font-size: 18px;
                }
            }
        }
    }

    &.option-2 {
        @include breakpoint(small) {
            padding: 0;
        }

        .box {
            height: 590px;

            @include breakpoint(wide) {
                height: 420px;
            }

            @include breakpoint(small) {
                height: 300px;
                margin: 0 0 6px 0;
                border-radius: $border-radius;
            }
        }

        .box-1 {
            width: calc(50% - 6px);

            @include breakpoint(small) {
                width: 100%
            }
        }

        .box-2 {
            width: calc(50% - 6px);

            @include breakpoint(small) {
                width: 100%
            }
        }

        .abs-content {
            h6 {
                @media screen and (min-width: 1000px) {
                    font-size: 18px;
                }
            }
        }
    }

    &.option-1 {
        .box {
            width: 100%;
        }
    }

    &.option-5 {

        .container {
            display: flex;
            flex-flow: column wrap;
            align-content: space-between;
            height: 580px;
            clear: both;

            @include breakpoint(medium) {
                height: 480px;
            }

            @include breakpoint(small) {
                display: block;
                height: auto;
            }

            &::before,
            &::after {
                content: "";
                flex-basis: 100%;
                width: 0;
                order: 2;
            }
        }

        .box {
            width: calc(33.33% - 6px);

            @include breakpoint(small) {
                width: 100%
            }

        }

        .box-3 {
            height: 565px;

            @include breakpoint(medium) {
                height: 445px;
            }

            @include breakpoint(small) {
                height: 150px;
                margin: 0 0 6px 0;
                border-radius: $border-radius;
            }
        }

        .box-1,
        .box-2,
        .box-4,
        .box-5 {
            height: 280px;

            @include breakpoint(medium) {
                height: 220px;
            }

            @include breakpoint(small) {
                height: 150px;
                margin: 0 0 6px 0;
                border-radius: $border-radius;
            }
        }
    }

    &.gallery-type {
        &.option-5 {
            .container {
                @include breakpoint(small) {
                    height: 160px;
                    display: flex;
                    padding-left: 9px;
                }
            }

            .box {
                @include breakpoint(small) {
                    width: calc(33.33% - 6px);
                    border-radius: $border-radius;
                }
            }

            .box-3 {
                @include breakpoint(small) {
                    height: 147px;
                }
            }

            .box-1,
            .box-2,
            .box-4,
            .box-5 {
                @include breakpoint(small) {
                    height: 71px;
                    margin: 0 0 5px 0;
                }
            }
        }
    }

    .link-wrap {
        display: none;

        @include breakpoint(small) {
            padding: 5px 3px 0;
            display: block;
        }
    }
}

/* end showcase-layout */


// slider-nav
.slider-nav {
    width: 80px !important;
    margin: 0 auto 0;
    text-align: center;

    .slick-list {
        padding: 0 !important;
    }

    .svg,
    img {
        display: inline-block;
        max-width: 8px;
    }

    .slick-current {

        .svg,
        img {
            width: 10px;
            max-width: 10px;
            height: 10px;
            display: inline-block;
            margin-top: -1px;
        }
    }

    .slick-slide {}

    .slick-prev,
    .slick-next {
        &:before {
            content: '';
            position: absolute;
            height: 20px;
            width: 20px;
            top: 2px;
            left: -2px;
            background: url('../../images/buttons-arrows-arrow-right-carousel.svg') no-repeat center;
        }
    }

    .slick-prev {
        &:before {
            background: url('../../images/buttons-arrows-arrow-left-carousel.svg') no-repeat center;
        }
    }

    &.hero-nav {
        float: left;
        z-index: 1;
        height: auto;

        .slick-prev,
        .slick-next {
            &:before {
                background: url('../../images/buttons-arrows-arrow-back-copy-2.svg') no-repeat center;
            }
        }

        .slick-prev {
            &:before {
                background: url('../../images/buttons-arrows-arrow-forward-copy.svg') no-repeat center;
            }
        }
    }

    .slick-slide {
        height: auto !important;
    }
}

.slick-slide {
    outline: none;

    &:focus {
        outline: none;
    }
}

