        }

        .btn {
            min-width: 170px;
            margin-right: 16px;

            &:last-child {
                margin-right: 0;
            }

            @include breakpoint(wide) {
                min-width: 156px;
                // font-size: 10px;
                padding: 9px 7px;
            }

            @include breakpoint(small) {
                margin: 0 7px 10px !important;
            }
            @media screen and (max-width: 330px) {
                margin: 0 0 10px !important;
            }

            &.btn-green {
                border: 2px solid transparent;

                &:hover {
                    border-color: $dark_cyan;
                }
            }
        }
    }

    .list-col {
        display: flex;
        align-items: center;
        flex-wrap: wrap;

        ul {
            margin: 0;
            padding: 0;
            padding-left: 17.5%;

            @include breakpoint(wide) {
                padding: 0;
            }

            @include breakpoint(small) {
                padding: 10px 0 0;
            }

            li {
                list-style-type: none;
                position: relative;
                margin-bottom: 27px;
                padding-left: 35px;

