                    font-size: 20px;
                    line-height: 1.2;
                }

                @include breakpoint(small) {
                    padding-left: 25px;
                    margin-bottom: 20px;
                }

                img,
                .svg {
                    // margin-right: 12px;
                    margin-right: 0;
                    vertical-align: middle;
                    position: absolute;
                    left: 0;

                    @include breakpoint(small) {
                        width: 16px;
                        top: 3px;
                    }
                }
