            width: 100%;
            text-align: center;

            @media screen and (min-width: 992px) {
                display: none;
            }
        }
    }

    .content-col {
        .button-wrap {
            @include breakpoint(medium) {
                display: none;
            }
        }
    }

    .list-col {
        .button-wrap {
            display: none;

            @include breakpoint(medium) {
                display: block;
                clear: both;
                width: 100%;
            }
        }
    }

    p {
        font-family: $font-primary;
        font-size: 16px;
        font-weight: normal;
        font-style: normal;
        line-height: 1.5;
        letter-spacing: normal;
        color: $color_text;

        @include breakpoint(small) {
            font-size: 14px;
        }
    }

    h6 {
        font-size: 16px;
        font-weight: 600;
        line-height: 1.5;
        letter-spacing: normal;
        color: $dark_pink;
        margin-bottom: 0;

        @include breakpoint(small) {
