/* end intro module */

/* zig-zag-module */
.left-right-wrapper {
    .container {
        max-width: 100%;
        padding: 0;

        @media screen and (min-width: 1600px) {
            width: 1440px;
            padding: 0;
        }
    }

    .text-wrap {
        display: flex;
        flex-wrap: wrap;
        align-items: stretch;

        &.right-image {
            .img-wrap {
                order: 2;

                @include breakpoint(small) {
                    order: 0;
                }
            }

            .content-wrap {
                order: 1;

                @media (min-width: 1200px) {
                    // padding: 20px 81px 20px 7%;
                    padding: 20px 74px 20px calc((100vw - 1155px) / 2);
                }

                @media (min-width: 1400px) {
                    padding: 20px 74px 20px calc((100vw - 1155px) / 2);
                }

                @media (min-width: 1600px) {
                    padding: 20px 74px 20px 148px;
                }

                @include breakpoint(small) {
                    order: 0;
                }
            }
        }

        &.left-image {
            .content-wrap {
                @media (min-width: 1200px) {
                    // padding: 20px 81px 20px 7%;
                    padding: 20px calc((100vw - 1155px) / 2) 20px 74px;
                }

                @media (min-width: 1400px) {
                    padding: 20px calc((100vw - 1155px) / 2) 20px 74px;
                }

                @media (min-width: 1600px) {
                    padding: 20px 148px 20px 74px;
                }
            }
        }

        .img-wrap {
            flex: 0 0 60%;
            max-width: 60%;

            @include breakpoint(small) {
                max-width: 100%;
                flex: 0 0 100%;
            }

            img {
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
        }

        .content-wrap {
            max-width: 40%;
            flex: 0 0 40%;
            display: flex;
            flex-direction: column;
            justify-content: center;

            @include breakpoint(wide) {
                padding: 20px 6.9%;
            }

            @include breakpoint(medium) {
                padding: 20px 3%;
            }

            @include breakpoint(small) {
                max-width: 100%;
                flex: 0 0 100%;
                padding: 20px 15px 31px;
            }

            @include breakpoint(extrasmall) {
                padding: 27px 15px 50px;
            }

            h4 {
                font-family: $font-secondary;
                font-size: 26px;
                font-weight: normal;
                line-height: 1.15;
                letter-spacing: normal;
                margin-bottom: 9px;
                color: $dark_pink;
                font-weight: bold;
                ;

                @include breakpoint(wide) {
                    font-size: 24px;
                }

                @include breakpoint(medium) {
                    font-size: 19px;
                    margin-bottom: 10px;
                }

                @include breakpoint(small) {
                    font-size: 18px;
                    line-height: 1.44;
                }
                sup {
                    font-weight: normal;
                    top: 0;
                    font-size: inherit;
                }
            }

            p {
                font-family: $font-primary;
                font-size: 16px;
                font-weight: normal;
                line-height: 1.5;
                letter-spacing: normal;

                @include breakpoint(medium) {
                    font-size: 14px;
                }

                @include breakpoint(extrasmall) {
                    //margin-bottom: 0;
                }

                &:not(:last-child) {
                    margin: 0 0 24px;
                }
            }

            .button-wrap {
                margin-top: -10px;
                .btn {
                    @include breakpoint(small) {
                        width: auto;
                    }
                }
            }
        }
    }
}

/* end zig-zag-module */

/* block */
.block {
    padding: 80px 0 77px;

    @include breakpoint(medium) {
        padding: 45px 0;
    }

    @include breakpoint(small) {
        padding: 35px 0;
    }

    .container {
        @include breakpoint(small) {
            padding: 0 23px;
        }
    }

    .full-cta {
        position: relative;

        .btn {
            min-width: 200px;
        }
    }
}

.full-cta {
    border-radius: $border-radius;
    padding: 63px 15px 62px;
    text-align: center;
    color: $white;
    width: 100%;
    position: relative;
    overflow: hidden;

    @include breakpoint(small) {
        padding: 37px 40px 32px;
    }

    @include breakpoint(iPhone5) {
        padding: 35px 20px 32px;
    }

    &:before {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        height: 100%;
        width: 100%;
        opacity: 0.3;
        background-image: linear-gradient(to bottom, $black 30%, rgba(0, 0, 0, 0) 87%);
        z-index: 1;
        pointer-events: none;
        border-radius: $border-radius;
    }

    h2 {
        color: $white;
        margin-bottom: 15px;
        line-height: 1.23;
    }

    p {
        color: $white;
        font-family: $font-primary;
        // margin-bottom: 27px;
        max-width: 543px;
        margin: 0 auto;
        font-weight: 600;

        @include breakpoint(small) {
            font-size: 12px;
        }

        br {
            @include breakpoint(small) {
                display: none;
            }
        }
    }

    .button-wrap {
        margin-top: 31px;

        @include breakpoint(small) {
            margin-top: 27px;
        }
    }

    .btn-link {
        min-width: inherit;

        img {
            width: 20px;
            height: 20px;
            margin-left: 5px;
            position: relative;
            @include transition(0.4s);
            left: 0;
        }

        &:hover {
            color: $white  !important;

            img {
                left: 7px;
            }
        }
    }

    .nest-inside {
        position: relative;
        z-index: 1;
    }
}

/* end block */
.blockContent_seoFaqs {
    padding-bottom: 80px;

    @include breakpoint(medium) {
        padding-bottom: 30px;
        text-align: center;
    }
}

.js .blockContent_seoFaqs {
