// sibebar-toggle-wrapper
.sibebar-toggle-wrapper {
    background-color: $white;
    padding: 43px 0 0;

    @include breakpoint(small) {
        padding: 5px 0 0;
    }

    .sibebar {
        padding: 0;
        background-color: transparent;

        @include breakpoint(small) {
            margin: 0 -15px;
            padding: 15px 15px 16px;
            background-color: $grey-light;
        }

        h4 {
            @include breakpoint(small) {
                font-family: $font-primary;
                font-size: 14px;
                font-weight: normal;
                line-height: 1.5;
                letter-spacing: normal;
                margin-bottom: 8px;
            }
        }

        ul {
            margin-left: 0;
            padding-left: 0;
            list-style-type: none;

            @media (min-width: 767px) {
                overflow: visible;
                height: auto;
                width: auto;
                float: none;
                margin: 0;
                border: 0;
            }

            @include breakpoint(small) {
                margin-bottom: 0;
                border-radius: $border-radius;
                overflow: hidden;
                height: 48px;
            }

            &.open {
                @include breakpoint(small) {
                    height: auto;
                    overflow: hidden;
                }

                li {
                    border-top: 0;
                }
            }

            &:after {
                @media (min-width: 767px) {
                    display: none;
                }

                @include breakpoint(small) {
                    right: 7px;
                    top: 13px;
                }
            }

            li {
                margin: 0;
                display: block;

                @media (min-width: 767px) {
                    padding: 0;
                    display: block !important;
                }

                @include breakpoint(small) {
                    width: 100%;
                    background-color: transparent;
                    border: 0;
                    padding: 0;
                }

                &.always {
                    @media (min-width: 767px) {
                        display: none !important;
                    }

                    a {
                        @include breakpoint(small) {
                            background-color: $white;
                            white-space: nowrap;
                            text-overflow: ellipsis;
                            overflow: hidden;
                            border-radius: $border-radius $border-radius 0 0;
                            // border-radius: 0;
                            border-bottom: 0;
                        }
                    }
                }

                &:last-child {
                    a {
                        
                        @include breakpoint(small) {
                            border-radius: 0 0 $border-radius $border-radius;
                            // border-radius: 0;
                            
                        }
                        &:hover, 
                        &.active {
                            border-bottom: 1px solid rgba(245,125,195,.3);
                        }
                    }
                }

                a {
                    // background-color: rgba($light_pink, 0.1);
                    background-color: $light_pink;
                    border: 1px solid rgba(245,125,195,.3);
                    border-bottom: 1px solid $white;
                    display: block;
                    padding: 13.5px 60px 13.5px 15px;
                    font-family: $font-secondary;
                    font-size: 16px;
                    font-weight: 600;
                    line-height: 1.3;
                    letter-spacing: normal;
                    color: $dark_pink;
                    transition: all 0.3s;
                    position: relative;
                    text-decoration: none;

                    @include breakpoint(small) {
                        font-weight: 400;
                        background-color: $grey-lighter;
                        border-bottom: 1px solid $grey-light;
                    }

                    &:hover,
                    &.active {
                        background-color: $white;
                        text-decoration: none;
                        border: 1px solid rgba(245,125,195,.3);
                        border-bottom: none;
                    }
                    &.active {
                        font-weight: 700;
                    }

                    &:after {
                        content: '';
                        // border-right: 2px solid $blue-ashy;
                        // border-bottom: 2px solid $blue-ashy;
                        background: url('../../images/icons-chevron-down.svg');
                        height: 24px;
                        width: 24px;
                        position: absolute;
                        top: 50%;
                        transform: translate(0%, -50%) rotate(-90deg);
                        right: 10px;

                        @include breakpoint(small) {
                            display: none;
                        }
                    }

                    @include breakpoint(medium) {
                        padding: 13.5px 40px 13.5px 15px;
                    }

                    @include breakpoint(small) {
                        padding: 11.5px 60px 11.5px 15px;
                        line-height: 1.5;
                    }
                }
            }
        }
    }

    .toggle-content-wrap {
        display: none;

        &.active-content {
            display: block;
        }

        h4 {
            margin-bottom: 18px;
            color: $dark_pink;

            @include breakpoint(small) {
                text-align: center;
            }
        }

        >h4 {
            @include breakpoint(small) {
                margin-top: 25px;
                margin-bottom: 25px;
            }
        }

        .accordion-wrap {
            margin-top: 0;

            .card-body {
                p {
                    margin-bottom: 6px;
                }
            }
        }
    }
}

