.site-header {
    position: relative;
    z-index: 99;
    .top-nav {
      background-color: $dark_pink;
      padding: 9px 0 7px;
      @include transition(0.4s);
      @include breakpoint(small) {
        padding: 10px 0 8px;
      }
      .text {
        position: relative;
        padding: 0 30px;
        @include breakpoint(small) {
          padding: 0 30px 0 0;
        }
        p {
          margin: 0;
          color: $white;
          font-family: $font-primary;
          font-size: 14px;
          line-height: 1.71;
          letter-spacing: 0px;
          text-align: center;
          font-weight: 600;
          @include breakpoint(small) {
            font-size: 12px;
            line-height: 1.33;
            text-align: left;
          }
          a {
            text-decoration: underline;
            color: $white;
          }          
        }
        .topnav-close {
          position: absolute;
          top: 0;
          right: 0;
          cursor: pointer;
          @include breakpoint(small) {
            top: -5px;
          }
          @include breakpoint(smaller) {
            top: 4px;
            right: -5px;
          }
          img {
            width: 24px;
            height: 24px;
          }
        }
      }
    }
    .main-header {
      position: absolute;
      width: 100%;
      z-index: 3;
      .container {
        position: relative;
      }
      .site-branding {
        padding: 35px 0 10px;
        
        display: flex;
        align-items: center;

        @include breakpoint(wide) {
          position: relative;
          z-index: 1;
        }
        @include breakpoint(small) {
          padding: 12px 0 10px;
        }
        .svg-link {
          svg{
            width: 200px;
          }
          @include breakpoint(small) {
            width: 90px;
            display: inline-block;
            svg{
              width: 90px;
              height: 18px;
            }
          }
          @include breakpoint(small) {
            img {
              width: 90px;
            }
          }
        }
        p {
          margin: 0;
        }
        .custom-logo-link {
          float: left;
          @include breakpoint(wide) {
            // padding-left: 16px;
            padding-top: 1px;
          }
          @media screen and (max-width: 299px) {
            padding-left: 0;
          }
          img {
            width: 200px;
            @include breakpoint(small) {
              width: 127px;
            }
            @media screen and (max-width: 330px) {
              width: 115px;
            }
          }
        }
        .search-right {
          display: flex;
          flex-wrap: wrap;
          justify-content: space-between;
          // float: right;
          padding-left: 55px;

          flex-grow: 1;
          max-width: 100%;

          @include breakpoint(wide) {
            justify-content: flex-end;
            padding-left: 0;
          }
          .button-wrap {
            padding-top: 4px;
            @include breakpoint(small) {
              padding-top: 0px;
              margin-top: -2px;
              margin-right: -4px;
            }
          }
          .btn {
            min-width: inherit;
            @include breakpoint(small){
              margin-left: 10px;
            }
          }
          .mobile-search-trigger{
            display: none;
            @include breakpoint(small){
              display: inline-block;
              padding-top: 2px;
            }
          }
        }
      }
    }
    &.stick-header-helper {
      position: relative;
      width: 100%;
      top: -100px;
      left: 0;
      @include transition(0.4s);
    }
    &.stick-header {
      position: fixed;
      top: 0;
      left: 0;
      width: 100%;
      z-index: 9999999;
      @include transition(0.4s);
      @include breakpoint(wide){
        top: 0;
      }
      .top-nav {
        display: none;
      }
      @media screen and (min-width: 1200px) {        
        .main-header {
          box-shadow: 0 2px 8px 0 rgba(0, 0, 0, 0.25);
          background-color: $color_text;
          padding: 14px 0;
          .site-branding {
            // float: left;
            // padding: 0;
            // margin-top: 6px;
            padding: 0;
            .search-right {
              // display: none;
            }
            .site-title {
              display: none;
            }
            .svg-link {
              svg {
                path {
                  fill: $color_text;
                }
              }
            }
          }
        }
        .main-navigation {
          // clear: none;
          // float: right;
          // display: block;
          // width: calc(100% - 190px);
          ul {
            li {
              sup,
              &.btn-parent {
                display: inline-block;
              }
              a {
                // font-size: 16px;
                // color: $color_text;
              }
              &.btn-parent {
                a {
                  background-color: $color_text;
                  color: $white;
                  font-family: $font-secondary;
                  font-size: 10px;
                  //min-width: inherit;
                  //padding: 9px 19px;
                  font-weight: bold;
                  min-width: 132px;
                  padding: 9px 10px;
                  &:hover {
                    background-color: $white;
                    color: $color_text;
                  }
                }
              }
            }
          }
          &.mobile{
            @media screen and (min-width: 1200px){
              display: none;
            }
          }
        }
      }
      .main-header {
        @include breakpoint(wide) {
          background: $color_text;
          @include transition(0.4s);
          .site-branding {
            padding: 10px 0;
            @include breakpoint(small) {
              padding: 12px 0 10px;
            }
          }
        }
      }
    }
  }
  .logged-in{
    &.admin-bar{
      .site-header{
        &.stick-header{
          // top: 0;
        }
      }
    }
  }